___CSS_LOADER_EXPORT___.push([module.id, `.img-btn-grp {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;

  p {
    text-align: center;
    max-width: 700px;
  }

  &:hover > .img-wrapper {
    border: 5px solid var(--primary);
    transform: scale(1.02);
    box-shadow: var(--glow);
    & > img {
      transform: scale(1.06) rotateZ(4deg);
    }
  }
  &:hover > button {
    box-shadow: var(--glow);
    transform: scale(1.05);
  }
}

.img-wrapper {
  overflow: hidden;
  width: 250px;
  aspect-ratio: 1 / 1;
  border: 3px solid var(--primary);
  border-radius: 50%;
  transition: transform 0.7s ease-in-out;

  & > img {
    transition: transform 1s ease-in-out;
    width: 100%;
    height: 100%;
  }
}
