  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center/cover;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;

  .img-btn-grp {
    gap: 16px;
  }
