nav {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--accent);
  color: var(--white);
  display: flex;
  justify-content: space-between;
  padding-inline: 2em;
  padding-block: 2px;
}

.dog-icon {
  width: 50px;
  aspect-ratio: 1 / 1;;

  &:hover {
    cursor: pointer;
  }
}

#nav-links {
  display: flex;
  gap: 1em;

  p:hover {
    cursor: pointer;
  }
}

h1 {
  font-size: 2em;
}

#feature-img {
  width: 425px;
  border: 5px solid var(--primary);
}
